*:where(:not(iframe, canvas, em, img, svg, video):not(svg *)) {
  all: unset;
  display: revert;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}




a,
button {
  cursor: pointer;
}

ol,
ul {
  list-style: none;
}

img {
  max-width: 100%;
  vertical-align: bottom;
}

table {
  border-collapse: collapse;
}

textarea {
  white-space: revert;
}


em { 
  font-style: italic;
  font-style: italic!important;
} 

i { 
  font-style: italic;
  font-style: italic!important;
} 

.panel em, .panel i {
  font-style: italic;
  font-style: italic!important;
}